import { VuexModule, Module, Mutation, Action, getModule } from 'vuex-module-decorators';
import { VKMiniAppAPI } from '@vkontakte/vk-mini-apps-api';
import bridgeMock from '@vkontakte/vk-bridge-mock';
import { VKApi } from '@/models/VKApi';

const _vk_Api = inFrame() ? new VKMiniAppAPI() : new VKMiniAppAPI(bridgeMock);
_vk_Api.initApp();

export const vkMiniApp = _vk_Api;
export const vkApi = new VKApi();

import { detectIsMobile } from './_utils';
import store from '@/store';

let vk_params = {
	vk_access_token_settings: 'notify',
	vk_app_id: '7350550',
	vk_are_notifications_enabled: '0',
	vk_is_app_user: '1',
	vk_is_favorite: '0',
	vk_language: 'ru',
	vk_platform: 'desktop_web',
	vk_ref: 'other',
	vk_user_id: '182023330'
};
let vk_sign;

export interface IVkState {
}

@Module({ dynamic: true, store, name: 'app' })
class Vk extends VuexModule implements IVkState {
	mobile = detectIsMobile();

	get isMobile() {
		return this.mobile;
	}

	get vkParams() {
		return vk_params;
	}

	get vk_app_id_int() {
		return parseInt(vk_params.vk_app_id);
	}

	@Mutation
	init(payload) {
		vk_params = payload.vk_params;
		vk_sign = payload.vk_sign;
	}

	@Action({ rawError: true })
	async paramsUserRegister() {
		const userInfo = await vkMiniApp.getUserInfo();
		return new Promise((resolve, reject) => {
			const obj = {
				'method': 'auth.register_vk_bridge_user',
				'params': {
					vk_params,
					vk_sign,
					'viewer_id': userInfo.id,
					'nickname': 'VK:' + userInfo.id,
					'profile': {
						'first_name': userInfo.first_name,
						'last_name': userInfo.last_name,
						'city': userInfo.city,
						'country': userInfo.country,
						'sex': userInfo.sex,
						'photo_100': userInfo.photo_100,
						'photo_200': userInfo.photo_200
					},
					'vk_profile': { 'vk_platform': vk_params.vk_platform }
				}
			};
			resolve(obj);
		});
	}

	@Action({ rawError: true })
	async paramsUserAuth() {
		return new Promise((resolve, reject) => {
			const obj = {
				'method': 'auth.auth_vk_bridge_user',
				'params': {
					vk_params,
					vk_sign
				}
			};
			resolve(obj);
		});
	}

	@Action({ rawError: true })
	friendsGet() {
		return new Promise(async (resolve, reject) => {
			try {
				const accessToken = await vkMiniApp.getAccessToken(this.vk_app_id_int, 'friends')
					.catch(e => reject(e));
				//@ts-ignore
				const access_token = accessToken.accessToken;
				const params = {
					access_token,
					'v': '5.100',
					'fields':'bdate,nickname, sex, status',
					age_from:50,
					age_to:55,
				};

				const result = await vkMiniApp.callAPIMethod('friends.get', params)
					.catch(e => reject(e));

				resolve(result);
			}catch (e) {
				reject(e)
			}
		});
	}


}

export const VkModule = getModule(Vk);

function inFrame() {
	try {
		return window.self !== window.top;
	} catch (e) {
		return true;
	}
}
